import React from "react";
import { studentName, techSkills } from "./Bienvenido";

const Div = () => {
  return (
    <div>
      <h1>soy Henry!</h1>
      <h3>{studentName}</h3>
      <ol>
        {techSkills.map((tech) => (
          <li>{tech}</li>
        ))}
      </ol>
    </div>
  );
};

export default Div;
